import React from "react"; // Importa la librera React
import ReactDOM from "react-dom/client"; // Importa ReactDOM para renderizar la aplicacin
import "./index.css"; // Importa los estilos globales (donde Tailwind se inicializara)
import "./scroll-fixes.css"; // Importa las correcciones especficas para problemas de scroll
import App from "./App"; // Importa el componente principal de tu aplicacin, App.js
import ErrorBoundary from "./components/ErrorBoundary"; // Importa el Error Boundary

// Crea un "root" de React para tu aplicacin.
// Esto es el punto donde tu aplicacin React se conectar al DOM del navegador.
const root = ReactDOM.createRoot(document.getElementById("root"));

// Renderiza tu componente principal (App) dentro del "root".
// <React.StrictMode> es un componente que ayuda a detectar problemas potenciales en la aplicacin.
// <ErrorBoundary> captura errores y previene que la aplicacin se rompa completamente.
root.render(
  <React.StrictMode>
    <ErrorBoundary>
      <App />
    </ErrorBoundary>
  </React.StrictMode>
);
