import React, { useMemo } from "react";
import { ChefHat } from "lucide-react";

/**
 * Footer
 * Componente de pie de pgina optimizado que muestra informacin de copyright
 * y branding de la aplicacin Recetario Mgico.
 *
 * Caractersticas:
 * - Posicin fija en la parte inferior de la pantalla
 * - Responsive design para mviles y desktop
 * - Ao dinmico actualizado automticamente
 * - Optimizado para evitar re-renders innecesarios
 * - Soporte para modo oscuro
 *
 * @returns {JSX.Element} Componente de pie de pgina
 */
const Footer = () => {
  // Ao actual memoizado para evitar clculos innecesarios en cada render
  const currentYear = useMemo(() => new Date().getFullYear(), []);

  return (
    <footer
      className="w-full bg-slate-900 border-t border-emerald-700 py-2 fixed bottom-0 left-0 z-40"
      role="contentinfo"
      aria-label="Pie de pgina de la aplicacin"
    >
      <div className="flex items-center justify-center gap-2 text-emerald-400 font-semibold text-xs sm:text-sm tracking-wide select-none px-2 text-center break-words">
        {/* cono principal de la aplicacin */}
        <ChefHat
          size={18}
          className="text-emerald-400 drop-shadow flex-shrink-0"
          aria-hidden="true"
        />

        {/* Texto de copyright y crditos */}
        <span className="font-semibold text-emerald-200 dark:text-emerald-400">
           {currentYear} derechos reservados | Recetario Mgico by{" "}
          <span className="text-white dark:text-emerald-300 font-bold">
            Axon.net &amp; IA
          </span>
        </span>
      </div>
    </footer>
  );
};

// Exporta el componente memoizado para prevenir re-renders innecesarios
// cuando las props del componente padre no han cambiado
export default React.memo(Footer);
